:root {
  --theme-height-header: 4.5rem;
  --theme-height-dvh-full: 100dvh;
  --theme-height-lvh-full: 100lvh;
  --theme-height-svh-full: 100svh;
  --theme-height-scrollbar: 7px;

  --theme-width-max-content: 1920px;
  --theme-width-scrollbar: 15px;

  --theme-height-svh-full--header: calc(
    var(--theme-height-svh-full) - var(--theme-height-header)
  );

  --theme-color-brand-primary: #01773d;
  --theme-color-brand-primary-contrast: #ffffff;
  --theme-color-brand-secondary: #cfa54b;
  --theme-color-brand-secondary-contrast: #ffffff;

  --theme-color-footer-background: #235a71;
}

@media (prefers-color-scheme: light) {
  :root {
    --theme-color-header-background: #fff;
    --theme-color-header-border: transparent;
    --theme-color-header-menu-background: transparent;

    --theme-color-list-item-background: transparent;
    --theme-color-list-item-border: transparent;

    --theme-color-section-background: transparent;
    --theme-color-section-border: transparent;
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    --theme-color-header-background: #fff;
    --theme-color-header-border: transparent;
    --theme-color-header-menu-background: transparent;

    --theme-color-list-item-background: transparent;
    --theme-color-list-item-border: transparent;

    --theme-color-section-background: transparent;
    --theme-color-section-border: transparent;
  }
}

body {
  font-family: "Nunito Sans", sans-serif;
}

